import { CLOUDY, RAIN, SNOW, SUN, THUNDER, DRIZZLE } from '../constants/weathers';

const getWeatherState = (weather) => {
    const { id } = weather[0];

    if (id < 300){
        return THUNDER;
    }
    else if (id < 400){
        return DRIZZLE;
    }
    else if (id < 500){
        return RAIN;
    }
    else if (id < 700){
        return SNOW;
    }
    else if (id < 800){
        return SUN;
    }
    else {
        return CLOUDY;
    }
};

const transformWeatherData = weather_data => {
    const { weather } = weather_data;
    const { humidity, temp } = weather_data.main;
    const { speed } = weather_data.wind;
    const weatherState = getWeatherState(weather);

    return {
        humidity,
        temperature: temp,
        weatherState,
        wind: `${speed} m/s`
    };
};

export default transformWeatherData;



