import TransformForecastData from "../services/transformForecastData";
import transformWeatherData from "../services/transformWeatherData";

export const SET_CITY = 'SET_CITY';
export const SET_FORECAST_DATA = 'SET_FORECAST_DATA';
export const GET_WEATHER_CITY = 'GET_WEATHER_CITY';
export const SET_WEATHER_CITY = 'SET_WEATHER_CITY';

const setCity = payload => ({ type: SET_CITY, payload });
const setForecastData = payload => ({ type: SET_FORECAST_DATA, payload });
const getWeatherCity = payload => ({ type: GET_WEATHER_CITY, payload });
const setWeatherCity = payload => ({ type: SET_WEATHER_CITY, payload });

const apiKey = "582220375e3dc678f9425d6d5207427a";
const url = "http://api.openweathermap.org/data/2.5";

export const setSelectedCity = payload => {
    return (dispatch, getSate) => {
        const url_forecast = `${url}/forecast?q=${payload}&appid=${apiKey}&units=metric`;
        // Activar indicador de busqueda en el state
        dispatch(setCity(payload));

        const state = getSate();
        const date = state.cities[payload] && state.cities[payload].forecastDataDate;
        const now = new Date();

        if(date && (now - date) < 1 * 60 * 1000) {
            return;
        }

        return fetch(url_forecast).then(
            data => data.json()
        ).then(forecast_data => {
            const forecastData = TransformForecastData(forecast_data);
            // Modificar el state con el resultado de la promise
            dispatch(setForecastData({ city: payload, forecastData }));
        });
    };
};

export const setWeather = payload => {

    return dispatch => {
        payload.forEach(city => {
            dispatch(getWeatherCity(city));

            const url_weather = `${url}/weather?q=${city}&appid=${apiKey}&units=metric`;
            fetch(url_weather).then( data => {
                return data.json();
            }).then( weather_data => {
                const weather = transformWeatherData(weather_data);
                dispatch(setWeatherCity({ city, weather }));
            });
        });
    };
};



